.contenido {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  grid-template-areas:
    "n n n"
    "b b b"
    "c c c"
    "m m m"
    "f f f";
}

.banner {
  grid-area: b;
  margin-left: 15%;
  margin-right: 15%;
  margin-top: 20px;
  letter-spacing: 2px;
  text-align: center;
  padding: 10px 0px;
  box-shadow: 2px 2px 10px black;
  text-shadow: 1px 1px 4px black;
  background: rgb(112, 33, 42);
  background: -moz-linear-gradient(
    180deg,
    rgba(112, 33, 42, 1) 0%,
    rgba(189, 74, 87, 1) 100%
  );
  background: -webkit-linear-gradient(
    180deg,
    rgba(112, 33, 42, 1) 0%,
    rgba(189, 74, 87, 1) 100%
  );
  background: linear-gradient(
    180deg,
    rgba(112, 33, 42, 1) 0%,
    rgba(189, 74, 87, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#70212a",endColorstr="#bd4a57",GradientType=1);
  color: white;
}

.banner {
  animation-name: titulos;
  animation-duration: 1s;
  animation-timing-function: ease;
}

@keyframes titulos {
  from {
    margin-bottom: 1000px;
  }
  to {
    margin-bottom: 0px;
  }
}

.content {
  grid-area: c;
  display: grid;
  grid-template-areas:
    "sm sm sm sm sm sm"
    "c1 c2 c3 c4 c5 c6"
    "con con con con con con"
    "pr pr pr pr pr pr";
  justify-content: center;
}

.sm {
  grid-area: sm;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 400px;
  margin-left: 15%;
  margin-right: 15%;
}

.sm img {
  border-radius: 180%;
  height: 250px;
  margin-bottom: 40px;
}

.sm p {
  margin: 0px 10%;
}

.c1,
.c2,
.c3,
.c4,
.c5,
.c6 {
  display: flex;
  align-self: center;
  justify-content: center;
  height: 200px;
  width: 200px;
}

.c1 {
  grid-area: c1;
  animation-name: cleft;
  animation-duration: 2s;
}
.c2 {
  grid-area: c2;
  animation-name: cleft;
  animation-duration: 2s;
}
.c3 {
  grid-area: c3;
  animation-name: cleft;
  animation-duration: 2s;
}

.c4 {
  grid-area: c4;
  animation-name: cright;
  animation-duration: 2s;
}

.c5 {
  grid-area: c5;
  animation-name: cright;
  animation-duration: 2s;
}

.c6 {
  grid-area: c6;
  animation-name: cright;
  animation-duration: 2s;
}

@keyframes cleft {
  from {
    margin-right: 1000px;
  }
  to {
    margin-right: 0px;
  }
}

@keyframes cright {
  from {
    margin-left: 1000px;
  }
  to {
    margin-left: 0px;
  }
}

.c1 img:hover,
.c2 img:hover,
.c3 img:hover,
.c4 img:hover,
.c5 img:hover,
.c6 img:hover {
  z-index: 1;
  transform: scale(1.2);
  transition: 200ms;
}

.con {
  grid-area: con;
  margin-top: 40px;
  text-align: center;
  margin-bottom: 40px;
}

.pr1,
.pr2,
.pr3 {
  height: 200px;
  width: 200px;
  border: 1px solid;
  text-align: center;
  padding: 20px;
  margin: 0px 3%;
  box-shadow: 2px 2px 10px black;
  background-color: rgba(189, 74, 87, 0.02);
}

.pr1 p,
.pr2 p,
.pr3 p {
  text-align: left;
}

.pr1 a,
.pr2 a,
.pr3 a {
  text-align: left;
  color: rgb(112, 33, 42);
}

.pr {
  grid-area: pr;
  display: flex;
  text-align: center;
  align-content: center;
  justify-content: center;
  margin-top: 10px;
}

.pr1:hover,
.pr2:hover,
.pr3:hover {
  transform: scale(1.2);
  transition: 200ms;
}

.menu {
  grid-area: m;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 400px;
  margin-left: 15%;
  margin-right: 15%;
  margin-top: 40px;
  text-align: center;
}

.menu input {
  background-color: rgba(189, 74, 87, 0.02);
  border: 2px solid rgb(112, 33, 42);
  width: 300px;
  margin-bottom: 20px;
}

.menu textarea {
  background-color: rgba(189, 74, 87, 0.02);
  border: 2px solid rgb(112, 33, 42);
  width: 300px;
  margin-bottom: 20px;
}

.menu button {
  background-color: rgba(189, 74, 87, 0.9);
  padding: 10px 20px;
  color: white;
  box-shadow: 1px 1px 5px #555;
  font-size: 15px;
}

.footer {
  grid-area: f;
  text-align: center;
  font-weight: bold;
}

.footer img {
  height: 50px;
  width: 50px;
  margin-top: 20px;
}

@media (max-width: 1256px) {
  .content {
    grid-template-areas:
      "sm sm sm sm sm sm"
      "c1 c1 c1 c2 c2 c2"
      "c3 c3 c3 c4 c4 c4"
      "c5 c5 c5 c6 c6 c6"
      "con con con con con con"
      "pr pr pr pr pr pr";
  }

  .c1,
  .c2,
  .c3,
  .c4,
  .c5,
  .c6 {
    width: 100%;
    animation-name: cleft;
  }
}

@media (max-width: 756px) {
  .banner {
    grid-area: b;
    margin-left: 5%;
    margin-right: 5%;
    margin-top: 20px;
    letter-spacing: 2px;
    text-align: center;
    padding: 10px 0px;
  }

  .pr {
    grid-area: pr;
    display: flex;
    text-align: center;
    align-content: center;
    justify-content: center;
    margin-top: 10px;
    flex-direction: column;
    align-self: center;
    align-items: center;
  }

  .pr1 {
    margin-top: 10px;
  }

  .pr2,
  .pr3 {
    margin-top: 40px;
  }
}

@media (max-width: 550px) {
  .c1,
  .c2,
  .c3,
  .c4,
  .c5,
  .c6 {
    height: 150px;
    width: 150px;
  }
  .content {
    grid-template-areas:
      "sm sm sm sm sm sm"
      "c1 c1 c1 c2 c2 c2"
      "c3 c3 c3 c4 c4 c4"
      "c5 c5 c5 c6 c6 c6"
      "con con con con con con"
      "pr pr pr pr pr pr";
  }

  .c1,
  .c2,
  .c3,
  .c4,
  .c5,
  .c6 {
    width: 100%;
    animation-name: cleft;
  }
}

@media (max-width: 346px) {
  .c1,
  .c2,
  .c3,
  .c4,
  .c5,
  .c6 {
    height: 100px;
    width: 100px;
  }
  .content {
    grid-template-areas:
      "sm sm sm sm sm sm"
      "c1 c1 c1 c2 c2 c2"
      "c3 c3 c3 c4 c4 c4"
      "c5 c5 c5 c6 c6 c6"
      "con con con con con con"
      "pr pr pr pr pr pr";
  }

  .c1,
  .c2,
  .c3,
  .c4,
  .c5,
  .c6 {
    width: 100%;
    animation-name: cleft;
  }
}
